*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Barlow', sans-serif;
}


.popup{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.2);
  backdrop-filter: blur(5px);

  display: flex;
  align-items: flex-start;
  justify-content: center;
  padding: 60px 0;

  font-family: 'Barlow', sans-serif;
  &-prank{
    &__content{
      background-color: #323A46;
      max-width: 600px;
      width: 100%;
      border-radius: 15px;
      overflow-y: auto;
      max-height: calc(100vh - 120px);
    }
    &__header{
      height: 140px;
      background-color: #3B4452;
      position: relative;
      display: flex;
      align-items: center;
      flex-direction: column;
      justify-content: center;
    }
    &__title{
      font-weight: 700;
      font-size: 30px;
      color:#fff;
      text-align: center;
    }
    &__queue{
      color:#fff;
      font-size: 16px;
      text-align: center;
      margin-top: 12px;
      span{
        font-weight: 700;
        color:#2AC880;
      }
    }
    &__main{
      padding: 0 104px;
      height: 100%;
    }
    &__block{
      padding: 30px 0;
    }
    &__block + &__block{
      border-top: 1px solid #3A4452;
    }
    &__checkbox + &__checkbox{
      margin-top: 10px;
    }
    &__footer{
      border-top: 1px solid #3A4452;
      padding: 30px 104px;
    }
    &__consent{
      padding: 10px 104px 40px 104px;
    }
    &__summary{
      border-top: 1px solid #3A4452;
      padding: 30px 104px;
    }
  }
  &-input + &-input{
    margin-top: 25px;
  }
  &-input{
    &__title{
      font-size: 14px;
      color:#Fff;
      span{
        color:#6D7883
      }
    }
    &__item{
      margin-top: 12px;
      width: 100%;
      border-radius: 5px;
      background: #3B4452;
      border: 1px solid #3A4452;
      height: 46px;
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 14px 11px;
    }
    &__input{
      width: 100%;
      height: 100%;
      border: none;
      outline: none;
      background-color: transparent;
      color:#fff;
      font-size: 14px;
      &:focus::placeholder{
        opacity: 0;
      }
      &::placeholder{
        color: #6D7883;
        opacity: 1;
        transition: .3s opacity;
      }
    }
    &__textarea{
      height: 100px;
      textarea{
        resize: none;
      }
    }
  }
  &-tips{
    &__currency{
      display: flex;
      align-items: center;
    }
    &__header{
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    &__text{
      color:#fff;
      font-size: 14px;
    }
    &__content{
      margin-top: 10px;
    }
    &__footer{
      margin-top: 20px;
    }
    &__noticy{
      font-size: 14px;
      line-height: 1.3;
      color:#2AC880;
    }
  }
  &-summary{
    &__wrapper{
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    &__text{
      font-size: 16px;
      color:#fff;
    }
    &__currency{
      display: flex;
      align-items: center;
    }
  }
  &-currency{
    &__item{
      display: flex;
      align-items: center;
    }
    &__item + &__item{
      margin-left: 20px;
    }
    &__img{
      flex-shrink: 0;
    }
    &__text{
      color:#fff;
      font-size: 16px;
      margin-left: 10px;
      font-weight: 700;
    }
  }
  &-submit{
    &__wrapper{
      display: flex;
      align-items: center;
      justify-content: center;

      margin-top: 25px;
    }
    &__btn + &__btn{
      margin-left: 10px;
    }
    &__btn{
      -webkit-appearance: none;
      width: 180px;
      height: 46px;
      border-radius: 5px;

      display: flex;
      align-items: center;
      justify-content: center;

      background: #2AC880;
      border:none;
      outline: none;

      color:#fff;
      font-size: 14px;
      font-weight: 700;
      &:hover{
        cursor: pointer;
      }
      img{
        margin-left: 10px;
      }
    }
  }
  &__close{
    position: absolute;
    width: 15px;
    height: 15px;
    top: 20px;
    right: 20px;
    &::before,&::after{
      content: '';
      position: absolute;
      top: 7.5px;
      height: 2px;
      width: 100%;
      display: block;
      background-color: #fff;
    }
    &::before{
      left: 0;
      transform: rotate(-45deg);
    }
    &::after{
      right: 0;
      transform: rotate(45deg);
    }
  }
}

.checkbox{
  &-currency + &-currency{
    margin-left: 20px;
  }
  &-consent + &-consent{
    margin-top: 15px;
  }
  &-block{
    background-color: #2A303A;
    border-radius: 5px;
    &__header{
      border-bottom: 1px solid #3A4452;
      padding: 16px 15px;
    }
    &__content{
      padding: 10px 15px;
    }
    &__text{
      font-size: 14px;
      line-height: 18px;
      color:#fff;
    }
  }
  &-label{
    &:hover{
      cursor: pointer;
    }
    &__input{
      display: none;
      &:checked + .checkbox-label__content{
        .checkbox-label__checkmark{
          border-color:#fff;
          &::before{
            opacity: 1;
          }
        }
      }
    }
    &__content{
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    &__text{
      font-weight: 700;
      font-size: 14px;
      color:#2AC880;
      display: flex;
      align-items: center;
      &-img{
        height: 25px;
        max-width: 32px;
        width: 100%;
        object-fit: contain;
        margin-right: 10px;
      }
    }
    &__checkmark{
      width: 18px;
      height: 18px;
      border-radius: 5px;
      border:1px solid #3A4452;
      position: relative;
      transition: .3s border-color;
      flex-shrink: 0;
      &::before{
        content: '';
        position: absolute;
        width: 12px;
        height: 8px;
        top: 50%;
        margin-top: -4px;
        left: 50%;
        margin-left: -6px;
        display: block;
        background:url("../img/check.svg") center center no-repeat;
        background-size: contain;
        opacity: 0;
        transition: .3s opacity;
      }
    }
    &__text + &__checkmark{
      margin-left: 10px;
    }
    &__checkmark + &__text{
      margin-left: 10px;
    }
  }
  &-consent{
    &__text{
      color:#989CA2;
      font-size: 14px;
      font-weight: 400;
      a{
        color:#fff;
        text-decoration: none;
        margin-left: 5px;
      }
    }
    &__content{
      justify-content: flex-start;
    }
  }
}

.tips{
  &-counter{
    text-align: center;
    font-size: 14px;
    color:#6D7883;
    &__content{
      width: 100%;
    }
    &__minus, &__plus{
      width: 46px;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
    }
    &__minus{
      border-right: 1px solid #323A46;
    }
    &__plus{
      border-left: 1px solid #323A46;
    }
    &__control{
      height: 100%;
    }
    &__content{
      max-width: 200px;
      margin: 0 auto;
    }
  }
}

@media screen and (max-width: 768px) {
  .popup{
    &-prank{
      padding: 30px 15px;
      &__content{
        max-height: calc(100vh - 60px);
      }
      &__main{
        padding: 0 15px;
      }
      &__consent{
        padding: 0 15px 30px 15px;
      }
      &__footer,&__summary{
        padding: 30px 15px;
      }
    }
  }
}

@media screen and (max-width: 375px) {
  .popup{
    &__close{
      top: 10px;
      right: 10px;
    }
  }
}